@import "../variables.scss";

.pageContainer {
  width: 100%;
  min-height: 100%;
  background-image: url("../../assets/images/background-with-logo.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  font-family: "mic-32";
  color: $textColor;
}

.scrollableContent {
  overflow-y: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.headerContainer {
  display: none !important;
  margin: 50px 0;
  display: flex;
  justify-content: center;
  width: 100%;
  position: sticky;
  top: -1px;
  z-index: 1;
  margin-top: 250px;
}

.header {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border-radius: $border-radius;
  padding: 5px;
  transition: background-color 0.2s ease-in-out;
}

.headerSticked {
  background-color: black;
}

.content {
  flex: 1;
}

$navLinkColor: $menuIconColor;

.navLink {
  color: $navLinkColor;
  text-decoration: none;
  font-size: 40px;
}

.mobileNavLink {
  color: $navLinkColor !important;
  text-decoration: none;
  font-size: 28px;
}

.navLink:visited {
  color: $navLinkColor;
}

.navLinkActive {
  color: $bandColor !important;
}

.mobileHeader {
  position: sticky;
  top: 10px;
  margin-left: 10px;
  // background-color: rgba(0, 32, 19, 0.7);
  transition: background-color 0.2s ease-in-out;
  width: 100px;
  padding: 10px;
  border-radius: $border-radius;
  z-index: 2;
}

.mobileHeaderOpen {
  background-color: $transparentBgColor;
}

.mobileMenuItems {
  display: flex;
  flex-direction: column;
  height: auto;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
}

.openMenuItems {
  max-height: 130px;
}

.closeMenuItems {
  max-height: 0;
}

.menuIcon {
  fill: $menuIconColor;
  stroke: #15aec4;
  transition: transform 0.2s ease-in-out;
}

.openMenuIcon {
  transform: rotate(90deg);
}

@media (min-width: $min-width) {
  .headerContainer {
    display: flex !important;
  }

  .mobileHeader {
    display: none;
  }
}
