@import "../variables.scss";

.container {
  margin-top: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tours {
  // padding: $padding;
  background-color: rgba(0, 44, 42, 0.7);
  border-radius: $border-radius;
  max-width: 300px;
}

.header {
  padding: 10px 0;
  font-size: 28px;
  position: sticky;
  top: 0;
  background-color: #001b1a;
  text-align: center;
}

.tourEntry {
  padding: 20px 0;
}

.list {
  text-align: center;
  padding: 0 30px;
}

@media (min-width: $min-width) {
  .header {
    top: 50px;
  }
}
